<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
          >
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column
          label=""
          prop="order_number"
          width="500"
        ></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-tag v-if="scope.row.pay_status === '1'" type="success"
              ></el-tag
            >
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" width="200">
          <template v-slot="scope">{{
            scope.row.create_time | dateFormat
          }}</template>
        </el-table-column>
        <el-table-column label="" width="150">
          <template>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showAddressDialog"
            ></el-button>
            <el-tooltip
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="success"
                icon="el-icon-location"
                size="mini"
                @click="showExpressDialog"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalOrders"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
    >
      <el-form :model="addressData" label-width="100px">
        <el-form-item label="/">
          <el-cascader
            :options="cityData"
            v-model="addressData.prov"
            :props="provProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addressData.detail"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="expressDialogVisible"
      width="50%"
    >
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(item, index) in expressInfo"
          :key="index"
          :timestamp="item.time"
        >
          {{ item.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="expressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="expressDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getOrderList, getExpressInfo } from 'network/orders.js'
import cityData from 'assets/citydata.js'

export default {
  name: 'Order',
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      totalOrders: 0,
      cityData,
      addressDialogVisible: false,
      expressDialogVisible: false,
      addressData: {
        prov: [],
        detail: ''
      },
      provProps: {
        expandTrigger: 'hover'
      },
      expressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await getOrderList(this.queryInfo)
      if (res.meta.status !== 200) return this.$msg.error(res.meta.msg)
      this.orderList = res.data.goods
      this.totalOrders = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    showAddressDialog() {
      this.addressDialogVisible = true
    },
    async showExpressDialog() {
      const { data: res } = await getExpressInfo()
      if (res.meta.status !== 200) return this.$msg.error(res.meta.message)
      console.log(res.data)
      this.expressInfo = res.data
      this.expressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>